.user {
  cursor: pointer;
  width: 100%;
  display: flex;
  & > * {
    margin-right: 10px;;
  }
  

  .userPrivate{
    display: flex;
    flex-direction: column;
    justify-content: center;
    width: 40px;
    height: 40px;
    // margin: 12px 25px;
    font-size: 1.7rem;
    color:#646464;

    &:hover{
      cursor: pointer;
    }
  }

  .privateNavDropDown {
    display:none;
  }
  
  .privateNavDropDownActive {
    display:flex;
    flex-direction: column;
    width: 200px;
    height: 180px;
    position: absolute;
    top: 72px;
    right: 0;
    opacity: 1;
    transition: all 0.5s ease;
    font-family: "Baloo Tamma 2", cursive;
    background-color: rgb(255, 255, 255);
    z-index: 9;
    box-shadow: rgba(0, 0, 0, .1) 5px 5px 15px 0px;

  }
}

